// pages/index.tsx
import * as React from "react";
import { Helmet } from "react-helmet";
import type { HeadFC, PageProps } from "gatsby";

import Header from "../Components/header";
import HeroSection from "../Components/hero";
import TestimonialSlider from "../Components/testimonial";
import ServiceCard from "../Components/Services";
import NewsletterSubscription from "../Components/NewsletterSubscription";
import Footer from "../Components/Footer";
import FAQSection from "../Components/Faq";
import ProjectManagementSection from "../Components/procces";
import AboutSection from "../Components/about";
import RecentProjects from "../Components/projects";
import ExitIntentPopup from "../Components/Exit";
import WarrantyBadgeSection from "../Components/WarrantyBadgeSection";

const IndexPage: React.FC<PageProps> = () => {
  return (
    <>
      {/* ‚úÖ SEO & Social Share Meta Tags */}
      <Helmet>
        <title>Bravo Roofing | Reliable Roofing in Idaho</title>
        <meta
          name="description"
          content="Bravo Roofing Idaho provides high-quality, trustworthy roofing services with expert craftsmanship and lasting results."
        />

        {/* Open Graph (Facebook, LinkedIn) */}
        <meta property="og:type" content="website" />
        <meta property="og:title" content="Bravo Roofing | Reliable Roofing in Idaho" />
        <meta
          property="og:description"
          content="Bravo Roofing Idaho provides high-quality, trustworthy roofing services with expert craftsmanship and lasting results."
        />
        <meta property="og:image" content="https://preview.bravoroofingidaho.com/social-share.png" />
        <meta property="og:url" content="https://preview.bravoroofingidaho.com/" />

        {/* Twitter Card */}
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content="Bravo Roofing | Reliable Roofing in Idaho" />
        <meta
          name="twitter:description"
          content="Bravo Roofing Idaho provides high-quality, trustworthy roofing services with expert craftsmanship and lasting results."
        />
        <meta name="twitter:image" content="https://preview.bravoroofingidaho.com/social-share.png" />
      </Helmet>

      <main>
        <ExitIntentPopup />
        <Header />
        <HeroSection />
        <WarrantyBadgeSection />
        <TestimonialSlider />
        <ServiceCard />
        <AboutSection />
        <ProjectManagementSection />
        <FAQSection />
        <RecentProjects />
        <Footer />
      </main>
    </>
  );
};

export default IndexPage;
